




























export default {
	data() {
		return {
			showMask: true
		}
	},
	props: {
		value: {}, //对应组件中的v-model
		firstTitle: {
			type: String,
			default: '大标题'
		},
		secondTitle: {
			type: String,
			default: '副标题'
		},
		promptInput: {
			type: String,
			default: ''
		}
	},
	methods: {
		closeMask() {
			this.showMask = false;
		},
		cancel() {
			this.$emit('cancel');
			this.closeMask();
		},
		confirm() {
			this.$emit('confirm');
			//this.closeMask();
			console.log("确认");
		}
	},
	mounted() {
		this.showMask = this.value;
	},
	watch: {
		value(newVal, oldVal) {
			this.showMask = newVal; //监听value值的变化
		},
		showMask(val) {
			this.$emit('input', val);
		}
	}
}



